
import immutable from "immutable"

const defaultState = immutable.fromJS({
    userInfo: {
        isLogin: false,
        token: "",
        mobile: ""
    }
})
export const user = (state = defaultState, action) => {
    switch (action.type) {
        case "getUserInfo":
            return state.set("userInfo", action.obj);
            break;
        default:
            return state;
            break;
    }
}