
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Foot } from '../../components/foot/'
import "./index.scss"
import { axios } from '@/axios'
import mobx from '~/mobx'
import { observer } from 'mobx-react';
@connect()
@observer
class Mine extends Component {
    constructor() {
        super()
        this.state = {
            list: [],
            avatar: ""
        }
    }
    componentDidMount() {
        axios.post("/react/getbooklists").then((result) => {
            this.setState({
                list: result.data.result
            })
        })
        mobx.getMsg()
        if (localStorage.avatar) {
            this.setState({
                avatar: localStorage.avatar
            })
        } else {
            this.setState({
                avatar: require("@/assets/images/shayu.jpg")
            })
        }
    }
    //删除留言板
    delMassage = (id) => {
        let obj = {
            _id: id
        }
        mobx.delMsg(obj)
    }
    //修改用户头像
    setUserImg = () => {
        const data = new FormData();
        const file = this.userinp.files[0];
        data.append("avatar", file);
        axios({
            url: "/react/uploadavatar?token=" + sessionStorage.token,
            method: "POST",
            data: data,
            contentType: false,
            processData: false,
        }).then(res => {
            const url = res.data.avatarUrl.replace(/public/, 'http://123.57.142.38:8866/');
            localStorage.avatar = url;

            this.setState({
                avatar: url
            })
        })

    }

    render() {
        const { list, avatar } = this.state;
        const { msgList } = mobx
        console.log(msgList)
        return (
            <div>
                <div className="m-top">
                    <div className="m-img">
                        <img ref={el => this.userimg = el} src={avatar} alt="" />
                        <label htmlFor="imgfile"></label>
                        <input type="file" id="imgfile" ref={el => this.userinp = el} onChange={this.setUserImg} />
                    </div>
                    <h3>admin</h3>
                </div>
                <h4 className="m-h4">账单类型</h4>
                <ul className="m-ul">
                    {list.map((e, i) => (
                        <li key={i}>
                            <i className={"iconfont " + e.icon}></i>
                            <span>{e.titel}</span>
                        </li>))}
                    <li style={{ transform: "scale(.7)", backgroundColor: "#eee", borderRadius: ".7rem" }}>
                        <i className="iconfont icon-hao"></i>
                    </li>
                </ul>
                <h4 className="m-h4">我的帖子</h4>
                <ul className="m-tie">
                    {msgList.map((e, i) => (
                        e.token == sessionStorage.token &&
                        <li style={{ padding: ".2rem .5rem", borderBottom: ".01rem solid #eee" }} key={i}>
                            <h3 style={{ lineHeight: ".5rem", marginBottom: ".1rem", textDecoration: "underline", textDecorationColor: "#666" }}>{e.title}</h3>
                            <div style={{ display: "flex", justifyContent: "space-between" }}>
                                <p style={{ width: "85%", maxHeight: "2rem", overflowWrap: "break-word", overflowY: "scroll" }}>{e.content}</p>
                                <p onClick={() => { this.delMassage(e._id) }}>删除</p>
                            </div>
                        </li>))}
                </ul>
                <Foot></Foot>
            </div>
        )
    }
}
export { Mine }
