import React, { Component } from 'react'
import "./index.scss"
import { history } from '../../../history'
const img = require('@/assets/images/welcome.png');
class Guide extends Component {
    
    render() {
        setTimeout(() => {
            history.push("/home")
        },4000)
        return (
            <div>
                <img src={img} className="g-img" alt=""/>
            </div>
        )
    }
}
export { Guide }