
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Grid, DatePicker, List } from 'antd-mobile';
import { axios } from '@/axios'
import { NavLink } from "react-router-dom";
import { NavBar } from 'antd-mobile';
import { history } from '@/history';
import "./index.scss"

@connect()
class Booking extends Component {
    constructor() {
        super()
        this.state = {
            list: [],
            flag: true,
            val: 0,
            cflag: false,
            txt: (<i className="iconfont icon-riqi riqi" style={{ fontSize: ".3rem" }} />),
            date: null
        }
    }
    componentDidMount() {
        axios.post("/react/getbooklists").then((result) => {
            this.setState({
                list: result.data.result
            })
        })
    }
    //左侧返回
    onLeft = () => {
        this.setState({
            flag: true
        })
        history.push("/home")
    }
    //计算器监听
    Calculation = () => {
        if (this.state.flag == true) {
            this.setState({
                flag: false,
                cflag: false
            })
            this.calcul.addEventListener("click", this.calcc)
        }
    }
    //计算器计算
    calcc = (e) => {
        let valStr = this.state.val.toString();
        switch (e.target.innerText) {
            case "":
                this.setState({
                    cflag: false
                })
                if (valStr.length <= 1) {
                    this.setState({
                        val: 0
                    })
                } else {
                    this.setState({
                        val: valStr.substring(0, valStr.length - 1)
                    })
                }
                break;
            case "+":
                this.setState({
                    cflag: false
                })
                if (valStr.indexOf("+") != "-1") {
                    let list = valStr.split("+")
                    this.setState({
                        val: (list[0] * 100 + list[1] * 100) / 100 + "+"
                    })
                } else if (valStr.indexOf("-") != "-1") {
                    let list = valStr.split("-")
                    this.setState({
                        val: ((list[0] * 100 - list[1] * 100) / 100 < 0 ? 0 : (list[0] * 1 - list[1] * 1)) + "+"
                    })
                } else if (valStr.substring(valStr.length - 1) * 1) {
                    this.setState({
                        val: valStr + "+"
                    })
                } else {
                    this.setState({
                        val: valStr + "+"
                    })
                }
                break;
            case "-":
                this.setState({
                    cflag: false
                })
                if (valStr.indexOf("+") != "-1") {
                    let list = valStr.split("+")
                    this.setState({
                        val: (list[0] * 100 + list[1] * 100) / 100 + "-"
                    })
                } else if (valStr.indexOf("-") != "-1") {
                    let list = valStr.split("-")
                    this.setState({
                        val: ((list[0] * 100 - list[1] * 100) / 100 < 0 ? 0 : (list[0] * 1 - list[1] * 1)) + "-"
                    })
                } else if (valStr.substring(valStr.length - 1) * 1) {
                    this.setState({
                        val: valStr + "-"
                    })
                } else {
                    this.setState({
                        val: valStr + "+"
                    })
                }
                break;
            case "完成":
                if (valStr.indexOf("+") == "-1" && valStr.indexOf("-") == "-1") {
                    console.log("完成")
                    this.calcul.removeEventListener("click", this.calcc)
                    this.setState({
                        val: 0,
                        flag: true
                    })
                    
                    let obj = {
                        token: sessionStorage.getItem("token"),
                        icon: this.props.history.location.pathname.split("/booking/")[1].split("/")[0],
                        title: this.props.history.location.pathname.split("/booking/")[1].split("/")[1],
                        beizhu: this.bz.value,
                        total: valStr,
                        date: this.state.date ? this.state.date.toLocaleDateString() : new Date().toLocaleDateString()
                    }
                    axios.post("react/bookinginfo", obj).then(() => {
                        history.push("/home")
                    })

                } else if (valStr.indexOf("+") != "-1") {
                    let list = valStr.split("+")
                    this.setState({
                        val: (list[0] * 100 + list[1] * 100) / 100,
                    })
                } else if (valStr.indexOf("-") != "-1") {
                    let list = valStr.split("-")
                    this.setState({
                        val: (list[0] * 100 - list[1] * 100) / 100 < 0 ? 0 : (list[0] * 1 - list[1] * 1)
                    })
                }
                this.setState({
                    cflag: true
                })
                break;
            case ".":
                this.setState({
                    cflag: false
                })
                if (valStr.substring(valStr.length - 1) * 1 || valStr.substring(valStr.length - 1) * 1 == 0) {
                    this.setState({
                        val: valStr + "."
                    })
                }
                break;
            default:
                if (this.state.cflag && e.target.innerText * 1 || this.state.cflag && e.target.innerText * 1 == 0) {
                    this.setState({
                        cflag: false,
                        val: e.target.innerText * 1
                    })
                } else {
                    this.setState({
                        cflag: false
                    })
                    if (e.target.innerText * 1 || e.target.innerText * 1 == 0) {
                        if (valStr == 0) {
                            this.setState({
                                val: e.target.innerText
                            })
                        } else {
                            this.setState({
                                val: valStr + "" + e.target.innerText
                            })
                        }
                    }
                }

                break;
        }
    }
    render() {
        const { list, val, cflag, flag } = this.state;

        return (
            <div>
                <NavBar
                    mode="light"
                    icon={"取消"}
                    onLeftClick={this.onLeft}
                    className="nav"
                >账单</NavBar>
                <Grid data={list}
                    columnNum={4}
                    renderItem={dataItem => (
                        <div style={{ margin: '.1rem', display: "flex", height: "100%", justifyContent: "center", alignItems: "center" }}>
                            <NavLink to={"/booking/" + dataItem.etitel + "/" + dataItem.titel} activeClassName="bookingnav-active">
                                <i className={"iconfont " + dataItem.icon} style={{ fontSize: ".75rem" }} alt="" onClick={this.Calculation} />
                                <div style={{ color: '#888', fontSize: '.14rem', marginTop: '.12rem' }}>
                                    <span>{dataItem.titel}</span>
                                </div>
                            </NavLink>
                        </div>
                    )}
                />
                {/* 计算器 */}
                <div className="calcul" style={{ display: flag ? "none" : "flex" }}>
                    <div className="calcul-top">
                        <div>
                            <i className="iconfont icon-beizhu" />
                            备注:
                        </div>
                        <input type="text" className="beizhu" ref={el => this.bz = el} placeholder="点击写点备注..." />
                        <div>{cflag ? val.toFixed(2) : val}</div> {/*  */}
                    </div>
                    <ul className="calcul-cont" ref={el => this.calcul = el}>
                        <li>7</li>
                        <li>8</li>
                        <li>9</li>
                        <li style={{ display: "flex", alignItems: "center", justifyContent: "center" }}>
                            <DatePicker
                                mode="date"
                                title="Select Date"
                                extra={this.state.txt}
                                value={this.state.date}
                                onChange={date => this.setState({ date })}
                            >
                                <List.Item arrow="horizontal">

                                </List.Item>
                            </DatePicker>
                        </li>
                        <li>4</li>
                        <li>5</li>
                        <li>6</li>
                        <li>+</li>
                        <li>1</li>
                        <li>2</li>
                        <li>3</li>
                        <li>-</li>
                        <li>.</li>
                        <li>0</li>
                        <li>
                            <i className="iconfont icon-delete" />
                        </li>
                        <li style={{ backgroundColor: "#FFDA44" }}>完成</li>
                    </ul>
                </div>
            </div>
        )
    }
}
export { Booking }
