
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Foot } from '../../components/foot/'
import "./index.scss"
import ReactEcharts from 'echarts-for-react';
import mobx from '~/mobx';
import { observer } from "mobx-react"
@connect(
    state => {
        return {
            list: state.getIn(["data", "list"])
        }
    }
)
@observer
class Chart extends Component {
    constructor() {
        super()
        this.state = {
            xdata: [],
            sdata: [],
            riyongtotal: 0,
            caiyintotal: 0,
            gouwutotal: 0,
            jianzhitotal: 0,
            licaitotal: 0

        }
    }
    componentDidMount() {
        let riyongtotal = 0;
        let caiyintotal = 0;
        let gouwutotal = 0;
        let jianzhitotal = 0;
        let licaitotal = 0;
        this.props.list.forEach(e => {
            if (e.title == "日用") {
                riyongtotal += e.total;
            } else if (e.title == "餐饮") {
                caiyintotal += e.total;
            } else if (e.title == "购物") {
                gouwutotal += e.total;
            } else if (e.title == "兼职") {
                jianzhitotal += e.total;
            } else if (e.title == "理财") {
                licaitotal += e.total;
            }
        });
        this.setState({
            xdata: this.props.list.map(e => e.date),
            sdata: this.props.list.map(e => e.total),
            riyongtotal,
            caiyintotal,
            gouwutotal,
            jianzhitotal,
            licaitotal
        })
    }
    getOption = () => {
        var option = {
            tooltip: {
                trigger: 'axis'
            },

            xAxis: {
                show: false,
                type: 'category',
                data: this.state.xdata,
                axisTick: {
                    interval: 0,
                },
            },
            yAxis: {
                type: 'value',
                axisLabel: {
                    color: "#000",
                    interval: 0,
                    formatter: function (value) {
                        if (value.length > 3) {
                            return value.substring(0, 3) + "...";
                        } else {
                            return value;
                        }
                    }
                }
            },
            series: [{
                data: this.state.sdata,
                type: 'line'
            }],

            legend: {
                orient: 'horizontal',
                x: 'center',
                y: 'top',
                borderWidth: 0,
                padding: 5,
                itemGap: 10,
                itemWidth: 20,
                itemHeight: 14,
                textStyle: {
                    color: '#333'
                }
            },

            series: [
                {
                    name: '支出',
                    type: 'line',
                    max: 9999,
                    symbolSize: 8,
                    hoverAnimation: false,
                    data: this.state.sdata
                }]
        };

        return option
    }
    getOptiontwo = () => {
        var option = {
            tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
                orient: 'vertical',
                x: 'left',
                data: ['日用', '餐饮', '购物', '兼职', '理财']
            },
            series: [
                {
                    name: '总支出',
                    type: 'pie',
                    radius: ['50%', '70%'],
                    avoidLabelOverlap: false,
                    label: {
                        normal: {
                            show: false,
                            position: 'center'
                        },
                        emphasis: {
                            show: true,
                            textStyle: {
                                fontSize: '30',
                                fontWeight: 'bold'
                            }
                        }
                    },
                    labelLine: {
                        normal: {
                            show: false
                        }
                    },
                    data: [
                        { value: this.state.riyongtotal, name: '日用' },
                        { value: this.state.caiyintotal, name: '餐饮' },
                        { value: this.state.gouwutotal, name: '购物' },
                        { value: this.state.jianzhitotal, name: '兼职' },
                        { value: this.state.licaitotal, name: '理财' }
                    ]
                }
            ]
        };

        return option
    }
    render() {
        const { bookingTotal } = mobx;
        return (
            <div>
                <div className="c-top">
                    <h3>鲨鱼记账</h3>
                </div>
                <div className="c-total">
                    <h3>总支出:  <span>{bookingTotal}</span></h3>
                </div>
                <div className="c-e">
                    <div className="c-echar">
                        <ReactEcharts option={this.getOption()} style={{ width: "calc(100vw - 0.26rem)", height: '4rem' }}></ReactEcharts>
                        <ReactEcharts option={this.getOptiontwo()} style={{ width: "calc(100vw - 0.26rem)", height: '4rem' }}></ReactEcharts>
                    </div>
                </div>
                <Foot></Foot>
            </div>
        )
    }
}
export { Chart }
