
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Foot } from '../../components/foot/'
import { observer } from "mobx-react"
import { axios } from "@/axios";
import { getBookingInfo } from '~/actions'
import immutable from "immutable"
import './index.scss'
import { Flex, WhiteSpace } from 'antd-mobile';
import mobx from "~/mobx"
@connect(
    state => {
        return {
            list: state.getIn(["data", "list"])
        }
    }
)
@observer
class Home extends Component {
    constructor() {
        super()
        this.state = {
            total: 0,
        }
    }
    componentDidMount() {
        let obj = {
            token: sessionStorage.getItem("token")
        }
        this.props.dispatch(getBookingInfo(obj))
        mobx.changeTotal()
    }

    render() {
        const { list } = this.props;
        const { bookingTotal } = mobx;
        return (
            <div>
                <div className="d-top">
                    <h3>鲨鱼记账</h3>
                    <div className="d-nav">
                        <div className="d-date">
                            <span>2019</span>
                            <i className="iconfont icon-riqi d-riqi"></i>
                        </div>
                        <div className="exp">
                            <span>支出:</span>
                            <span className="money">-{bookingTotal}</span>
                        </div>
                    </div>
                </div>
                <ul className="d-ul">
                    {list.map((e, i) => (<li key={i}>
                        <h4>{e.date}</h4>
                        <div className="d-li-info">
                            <i className={"iconfont d-i icon-" + e.icon}></i>
                            <span>{e.beizhu ? e.beizhu : e.title}</span>
                            <p>-{e.total}</p>
                        </div>
                    </li>))}
                </ul>
                <Foot></Foot>
            </div>
        )
    }
}
export { Home }
