import React, { Component } from 'react'
import { Switch, HashRouter, Route, Redirect } from 'react-router-dom'
import { Home } from './view/home'
import { Guide } from './view/guide';
import { Chart } from './view/chart';
import { Booking } from './view/booking';
import { Mine } from './view/mine'
import { Community } from './view/community'
import { Login } from './view/login';

class App extends Component {
    render() {
        return (
            <div>
                <HashRouter
                    basename="/">
                    <Layout />
                </HashRouter>
            </div>
        )
    }
}

export class Layout extends Component {
    render() {
        return (
            <div>
                <Switch>
                    <Route path="/" exact render={() => <Redirect to="/guide" />} />
                    <Route path="/home" strict component={Home} />
                    <Route path="/guide" strict component={Guide} />
                    <Route path="/chart" strict component={Chart} />
                    <Route path="/booking" strict component={Booking} />
                    <Route path="/community" strict component={Community} />
                    <Route path="/mine" strict component={Mine} />
                    <Route path="/login" strict component={Login} />
                    <Route render={() => <Redirect to="/home" />} />
                </Switch>
            </div>
        )
    }
}
export { App }