import React, { Component } from 'react';
import { WingBlank, WhiteSpace, Button, InputItem, List } from 'antd-mobile';
import { axios } from "@/axios"
import "./index.scss"
import { connect } from "react-redux";
import immutable from "immutable"
import users from '~/mobx'
@connect()
class Login extends Component {
    constructor() {
        super()
        this.state = {
            isCode: true,   //是否允许点击验证码
            isM: false,      //手机号是否合法
            getCode: false, //是否正在发送验证码请求中
            isC: false,      //验证码是否合法
            isLogin: true,   //是否允许登陆
            count: 60,
            codeTxt: "获取验证码"
        }
    }
    //判断手机号
    isMobile = (mobile) => {
        let mReg = /^1(3|5|7|8|9)\d{9}$/;
        this.setState({
            isM: mReg.test(mobile) ? true : false
        })
        if (this.state.getCode == false) {
            this.setState({
                isCode: !mReg.test(mobile)
            })
        }
    }
    //判断验证码
    isCode = (code) => {
        let cReg = /^\d{4}$/;
        new Promise((resolve, reject) => {
            this.setState({
                isC: cReg.test(code) ? true : false
            })
            resolve()

        }).then(() => {
            if (this.state.isC && this.state.isM) {
                this.setState({
                    isLogin: false
                })
            } else {
                this.setState({
                    isLogin: true
                })
            }
        })
    }

    //点击获取验证码
    getCode = () => {
        const mobile = this.mobile.state.value;
        const code = this.code.state.value;
        axios.post("/react/code", {
            mobile,
            code
        })
        this.setState({
            getCode: true,
            isCode: true
        })
        var timer = setInterval(() => {
            this.setState({
                count: --this.state.count
            })
            if (this.state.count == 0) {
                this.setState({
                    getCode: false,
                    isCode: false,
                    count: 60
                })
                clearInterval(timer)
            }
        }, 1000)
    }

    //点击登录
    toLogin = () => {
        const mobile = this.mobile.state.value;
        const code = this.code.state.value;
        axios.post("/react/ifcode", {
            mobile,
            code
        }).then((res) => {
            if (res.data.code == 200) {
                var token = res.data.token;
                sessionStorage.token = token;
                var obj = immutable.fromJS({
                    token: token
                })
                axios.post("/react/users", obj).then((res) => {
                    if (res.data.result.avatar) {
                        const url = res.data.result.avatar.replace(/public/, 'http://123.57.142.38:8866/');
                        localStorage.avatar = url
                    }
                    users.changeUserInfo(res.data.result)
                })
                this.props.history.push("/home");
            }
        })
    }
    render() {
        const {
            isCode,
            isLogin,
            getCode,
            count,
            codeTxt
        } = this.state
        return (
            <div>
                <List className="loginList">
                    <WingBlank>
                        <InputItem
                            type="number"
                            maxLength="11"
                            placeholder="请输入手机号"
                            onChange={this.isMobile}
                            ref={el => this.mobile = el}
                        >
                            <p className="lab">手机号</p>
                        </InputItem>
                    </WingBlank>
                    <WingBlank>
                        <InputItem
                            type="number"
                            maxLength="4"
                            placeholder="请输入验证码"
                            onChange={this.isCode}
                            ref={el => this.code = el}
                        >
                            <button
                                className={isCode ? "codeBtn ing" : "codeBtn"}
                                disabled={isCode}
                                onClick={this.getCode}>
                                {getCode ? count + "s" : codeTxt}
                            </button>
                        </InputItem>
                    </WingBlank>
                    <WingBlank>
                        <Button
                            className={isLogin ? "loginBtn ing" : "loginBtn"}
                            onClick={this.toLogin}>登陆</Button>
                    </WingBlank>
                </List>
            </div>
        )
    }
}

export { Login }