
import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Foot } from '../../components/foot/'
import { axios } from '@/axios'
import { Button } from 'antd-mobile'
import "./index.scss"
import mobx from '~/mobx';
import { observer } from 'mobx-react';
@connect()
@observer
class Community extends Component {
    constructor() {
        super()
        this.state = {
            posting: true,
            msgBtn: true
        }
    }
    //获取留言列表
    componentDidMount() {
        mobx.getMsg()
    }
    //新增留言
    addMassage = () => {
        let obj = {
            token: sessionStorage.token,
            title: this.title.value,
            content: this.content.value
        }
        mobx.addMsg(obj)
        this.cancel()
    }
    //删除留言板
    delMassage = (id) => {
        let obj={
            _id:id
        }
        mobx.delMsg(obj)
    }
    //打开留言板
    openMassage = () => {
        this.setState({
            posting: !this.state.posting
        })
    }
    //关闭留言板
    cancel = () => {
        this.title.value = ""
        this.content.value = ""
        this.setState({
            posting: !this.state.posting,
            msgBtn: true
        })
    }
    //判断是否填写内容
    msgBtn = () => {
        if (this.title.value && this.content.value) {
            this.setState({
                msgBtn: false
            })
        } else {
            this.setState({
                msgBtn: true
            })
        }
    }
    render() {
        const {
            posting,
            msgBtn
        } = this.state
        const { msgList } = mobx
        return (
            <div>
                <div className="com-top">
                    <h3>鲨鱼社区</h3>
                </div>
                <ul>
                    {msgList.map((e, i) => (<li style={{ padding: ".2rem .5rem", borderBottom: ".01rem solid #eee" }} key={i}>
                        <h3 style={{ lineHeight: ".5rem", marginBottom: ".1rem", textDecoration:"underline",textDecorationColor:"#666" }}>{e.title}</h3>
                        <div style={{ display: "flex", justifyContent: "space-between" }}>
                            <p style={{width:"85%",maxHeight:"2rem", overflowWrap:"break-word",overflowY:"scroll"}}>{e.content}</p>
                            {e.token == sessionStorage.token && <p onClick={() => { this.delMassage(e._id)}}>删除</p>}
                        </div>
                    </li>))}
                </ul>
                {posting ? <div style={{
                    position: "fixed",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center",
                    width: "1rem",
                    height: "1rem",
                    bottom: "2rem",
                    right: "1rem",
                    backgroundColor: "#FFDA44",
                    borderRadius: "50%"
                }}
                    onClick={this.openMassage}
                >发帖</div> : <div
                    style={{
                        borderTop: ".03rem solid #ddd",
                        borderRadius: ".5rem .5rem 0 0",
                        position: "fixed",
                        bottom: "0",
                        width: "100%",
                        zIndex: "999",
                        backgroundColor: "#eee"
                    }}>
                        <h3 style={{ lineHeight: "1rem", textAlign: "center" }}>发帖</h3>
                        <div style={{ padding: ".2rem" }}>
                            <div style={{ borderBottom: ".02rem solid #ddd", display: "flex", alignItems: "center", paddingBottom: ".1rem" }}>
                                <span style={{ color: "#666" }}>用户昵称:</span>
                                <input type="text"
                                    ref={el => this.title = el}
                                    style={{ width: "4.7rem", height: ".7rem", border: ".01rem solid #ddd", marginLeft: ".4rem", padding: ".2rem", boxSizing: "border-box" }}
                                    placeholder="给自己起个有趣的昵称吧..."
                                    onChange={this.msgBtn} />
                            </div>
                            <div style={{ padding: ".4rem 0 .2rem", display: "flex", alignItems: "center", borderBottom: ".02rem solid #ddd" }}>
                                <span style={{ color: "#666" }}>我的点评:</span>
                                <textarea type="text"
                                    ref={el => this.content = el}
                                    style={{ width: "4.5rem", height: "2rem", padding: ".1rem", border: ".01rem solid #ddd", marginLeft: ".4rem" }}
                                    placeholder="把你的心得分享给大家吧..."
                                    onChange={this.msgBtn} />
                            </div>

                            <Button style={{
                                width: "60%", margin: "auto",
                                marginTop: ".3rem",
                                backgroundColor: "#FFDA44",
                                color: "#fff"
                            }}
                                disabled={msgBtn}
                                onClick={this.addMassage}>提交</Button>
                            <Button style={{
                                width: "60%", margin: "auto",
                                marginTop: ".3rem",
                                backgroundColor: "#999",
                                color: "#eee"
                            }}
                                onClick={this.cancel}>取消</Button>
                        </div>
                    </div>}
                <Foot></Foot>
            </div>
        );
    }
}
export { Community }