import { observable, action, computed, autorun } from "mobx";
import { axios } from '@/axios';
class Info {
    //用户信息
    @observable userInfo = {};
    //获取用户信息
    @action changeUserInfo = (obj) => {
        this.userInfo = obj;
    }
    //支出总计
    @observable bookingTotal = 0;
    //获取支出总计
    @action changeTotal = () => {
        let obj = {
            token: sessionStorage.getItem("token")
        }
        axios.post("react/gettotal", obj).then((res) => {
            this.bookingTotal = res.data.result;
        })
    }
    //留言列表
    @observable msgList = [];
    //添加留言
    async addMsg(obj) {
        const res = await axios.post("react/addmsg", obj)
        this.getMsgList(res.data.result)
    }
    //获取留言
    async getMsg() {
        const res = await axios.post("react/msglist")
        this.getMsgList(res.data.result)
    }
    //删除留言
    async delMsg(obj){
        const res = await axios.post("react/delmsg", obj)
        this.getMsgList(res.data.result)
    }
    @action getMsgList = (list) => {
        this.msgList = list
    }

}
export default new Info()