

import React, { Component } from 'react'
import "./index.scss";
import { NavLink } from "react-router-dom";
import {history} from "@/history"

export const foots = [
    { txt: "明细", path: "/home", name: "detailed", icon: "icon-mingxi" },
    { txt: "图表", path: "/chart", name: "chart", icon: "icon-tubiaozhexiantu" },
    { txt: "记账", path: "/booking", name: "booking", icon: "booking" },
    { txt: "社区", path: "/community", name: "community", icon: "icon-quedingshequ" },
    { txt: "我的", path: "/mine", name: "mine", icon: "icon-gerenzhongxin" }
];


class Foot extends Component {
    componentDidMount(){
        if(!sessionStorage.token){
            history.push("/login")
        }
    }
    render() {
        return (
            <footer>
                {
                    foots.map((foot, i) => {
                        return (
                            <div key={i}>
                                <NavLink to={foot.path} activeClassName="nav-active">
                                    <i className={"iconfont " + foot.icon}></i>
                                    <span>{foot.txt}</span>
                                </NavLink>
                            </div>
                        )
                    })
                }
            </footer>
        )
    }
}
export { Foot }