import { axios } from "@/axios"

export const getUserInfo = (obj) => {
    return {
        type: "getUserInfo",
        obj
    }
}

//获取账单列表
export async function getBookingInfo(obj) {
    const res = await axios.post("/react/getbooking", obj)
    return {
        type: "getBookingInfo",
        data: res.data.result
    }
}